.game {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 0 0.3rem;
  border-radius: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
  width: 100%;
  box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.game h1 {
  text-align: center;
}

.game div.game-board {
  position: relative;
  display: grid;
  grid-template-columns: repeat(3, 100px);
  -webkit-justify-content: center;
          justify-content: center;
}

.game button.square {
  position: relative;
  white-space: nowrap;
  border: 0.0625rem solid goldenrod;
  text-transform: capitalize;
  border-radius: 0.15rem;
  text-align: center;
  min-height: 100px;
  min-width: 100px;
  font-size: 2rem;
  transition: all, 0.3s;
}

.game button.square:hover {
  background: mediumaquamarine;
}

.game div.restart-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.game button.restart-game {
  position: relative;
  white-space: nowrap;
  border: 0.0625rem solid goldenrod;
  text-transform: capitalize;
  border-radius: 0.15rem;
  text-align: center;
  min-height: 100px;
  min-width: 100px;
  font-size: 1.50rem;
  background-color: palevioletred;
  color: whitesmoke;
}

.game button.restart-game:hover {
  background: linear-gradient(0deg, palevioletred, rgba(219,112, 147, 0.3));
  transition: all, 0.3s;
}
.cards {
    padding: 4rem;
    background: honeydew;
    font-family: 'Raleway', sans-serif;
  }
  
  h1 {
    text-align: center;
  }
  
  .cards-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-align-items: center;
            align-items: center;
    max-width: 1120px;
    width: 90%;
    margin: 0 auto;
  }
  
  .cards-wrapper {
    position: relative;
    margin: 50px 0 45px;
  }
  
  .cards-items {
    margin-bottom: 24px;
  }
  
  .cards-item {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 0 1rem;
    border-radius: 10px;
  }
  
  .cards-item-link {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none !important;
  }
  
  .cards-item-pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
  }
  
  .fade-img {
    -webkit-animation-name: fade-img;
            animation-name: fade-img;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
  }
  
  .cards-item-pic-wrap::after {
    content: attr(data-category);
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 400;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
  }
  
  .cards-item-img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: scale-down;
    transition: all 0.2s linear;
  }
  
  .cards-item-img:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
  }
  
  .cards-item-info {
    padding: 20px 30px 30px;
  }
  
  .cards-item-text {
    color: #252e48;
    font-size: 18px;
    font-weight: 500;
    line-height: 24px;
  }
  
  @media only screen and (min-width: 1200px) {
    .content-blog-container {
      width: 84%;
    }
  }
  
  @media only screen and (min-width: 1024px) {
    .cards-items {
      display: -webkit-flex;
      display: flex;
    }
  }
  
  @media only screen and (max-width: 1024px) {
    .cards-item {
      margin-bottom: 2rem;
    }
  }
